import { Component,ViewChild } from '@angular/core';
import { NavController,Nav } from 'ionic-angular';
import {LandingPage} from '../landing/landing';
import {ProfilePage} from '../profile/profile';
import { LoginPage } from '../login/login';
import { s } from '@angular/core/src/render3';
import { MenuPage } from '../menu/menu';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class 
HomePage {

  // public rootPage:LoginPage
  @ViewChild(Nav) nav: Nav;
  constructor(public navCtrl: NavController,
              ) {

  }
  ionViewDidLoad(){
    this.checkLocal()
   }

  checkLocal(){
    let currentUserHospital=JSON.parse(localStorage.getItem('currentUserHospital'))
    if(currentUserHospital && currentUserHospital.token){
      this.navCtrl.setRoot(MenuPage)
     
     }else if(!currentUserHospital || currentUserHospital.token===null){
    this.navCtrl.setRoot(LoginPage)
     }
   }
}



