import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController } from 'ionic-angular';
import { LandingPage } from '../landing/landing';
import { patientInfo, mlcInfo, examinationDetails, personBrought } from '../../module/admissionDetails';
import { Content } from 'ionic-angular';
import { LoadingController } from 'ionic-angular';
import { NetworkProvider } from '../../providers/network/network';
import { Storage } from '@ionic/storage';
import { environment } from '../../environment/environment';



/**
 * Generated class for the ArCreatePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-ar-create',
  templateUrl: 'ar-create.html',
})
export class ArCreatePage {

  public dateOfToday = new Date().toISOString().slice(0, 10)

  @ViewChild(Content) content: Content;

  mlcIpNumber: String

  patientInformation = {} as patientInfo

  mlcInfo = {} as mlcInfo

  examination = {} as examinationDetails

  personBrought = {} as personBrought

  currentForm: number;

  pageTittleName: string;

  public loader: any

  currentUserHospital: any;

  uniqueNumber: any = "AR" + String(Date.now() + Math.floor(Math.random() * 10000));

  arLists: any;

  availOfRTA: boolean = false

  public baseUrl = environment.baseUrl;

  public arCreateUrl = "/api/v1/accidentRegisters/addAR"
  typeOfInjury: boolean;
  typeOfVechile: boolean;
  typeOfPatientPosition: boolean;


  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public loadingCtrl: LoadingController,
    public storage: Storage,
    public alertCtrl: AlertController,
    public dataService: NetworkProvider) {
  }

  ionViewDidLoad() {
    this.currentForm = 1;
    this.getTitle();
    this.getcurrentUserHospital()

  }
  goHome() {
    this.navCtrl.setRoot(LandingPage)
  }
  alert(message) {
    let alert = this.alertCtrl.create({
      subTitle: message,
      buttons: ['OK']
    }).present();
    // setTimeout(() => alert.present(), 1000 / 1000);
    // setTimeout(() => alert.dismiss(), 2000 / 4);
  }
  loaderFunction() {
    this.loader = this.loadingCtrl.create({
      content: `Please Wait...`,
    });
  }
  goBack() {
    this.loaderFunction()
    this.loader.present()
    this.content.scrollToTop();
    let currentForm = this.currentForm - 1
    if (currentForm == 1) {
      this.pageTittleName = "Patient Information";
    } else if (currentForm == 2) {
      this.pageTittleName = "MLC Information";
    }
    if (currentForm == 0) {
      this.navCtrl.setRoot(LandingPage)
    } else {
      this.currentForm = currentForm
    }
    setTimeout(() => {
      this.loader.dismiss()
    }, 1000)
  }
  getTitle() {
    if (this.currentForm == 1) {
      this.pageTittleName = "Patient Information";
    }
  }
  getcurrentUserHospital() {
    this.currentUserHospital=JSON.parse(localStorage.getItem('currentUserHospital')).userDetails
  }
  comman() {
    if (this.validateForm()) {
      this.alert("Please fill Mandatory* fields");
      return;
    }
    this.currentForm = 1 + this.currentForm;
    this.content.scrollToTop();
    if (this.currentForm == 1) {
      this.pageTittleName = "Patient Information";
    } else if (this.currentForm == 2) {
      this.pageTittleName = "MLC Information";
    } else if (this.currentForm == 3) {
      this.pageTittleName = "Examination Details"
    }
    if (this.currentForm === 2 || this.currentForm === 4) {
      this.createDetails()
    }
  }
  checkRTA(v) {
    let value = v
    this.availOfRTA = value.some(x => x == "Road Traffic Accident")
    if (this.availOfRTA == false) {
      this.mlcInfo.patientPosition = 'No need'
      this.mlcInfo.patientType = 'No need'
      this.mlcInfo.vechileNumber = 'No need'
      this.mlcInfo.vechileType = 'No need'
    }
  }
  isInformedToPolice(v) {
    let data = v;
    if (data == "No") {
      this.mlcInfo.toWhom = "No One"
    }
  }
  vechileType(v) {
    let data = v;
    if (data == "Others/") {
      this.typeOfVechile = true
    }
    else {
      this.typeOfVechile = false
      this.mlcInfo.otherVechileType = ''

    }
  }
  patientType(v) {
    let data = v;
    if (data == "Others/") {
      this.typeOfPatientPosition = true
    }
    else {
      this.typeOfPatientPosition = false
      this.mlcInfo.otherPatientPosition = ''

    }
  }
  injuryType(v) {
    let data = v;
    if (data == "Others/") {
      this.typeOfInjury = true
    }
    else {
      this.typeOfInjury = false
      this.examination.otherTypeOfInjury = ''

    }
  }

  checkOut(v) {
    let data = v
    if (data != "Referred Out") {
      this.examination.referralInstitutionType = 'No'
      this.examination.reasonforReferral = 'No'
    }
  }
  validateForm() {
    if (this.currentForm == 1) {
      if (!this.patientInformation.arrivalDate || !this.patientInformation.name ||
        !this.patientInformation.age || !this.patientInformation.gender) {
        return true
      }
    }
  }
  createDetails() {
    this.loaderFunction()
    this.loader.present();
    let data = {
      'arNumber': this.uniqueNumber,
      'currentUserID': this.currentUserHospital._id,
      'patientInformation': this.patientInformation,
      'personBrought': this.personBrought,
      'mlcInformation': this.mlcInfo,
      'examinationDetails': this.examination,
      'hospitalDetails': this.currentUserHospital,
      'completed': this.currentForm == 4 ? true : false
    }
    if (!navigator.onLine) {
      this.storage.get('arLists').then(localdata => {

        if (localdata) {
          if (Array.isArray(localdata)) {
            localdata = localdata.filter(item => item.arNumber != data.arNumber)
            localdata.push(data)
          }
        }
        else {
          localdata = [data]
        }
        this.storage.set('arLists', localdata).then(success => {
          console.log(this.arLists)
          if (data.completed == true) {
            this.alertCtrl.create({
              subTitle: 'Stored in Offline',
              buttons: [
                {
                  text: 'OK',
                  role: 'submit',
                  handler: () => {
                    this.navCtrl.setRoot(LandingPage)
                  }
                },
              ],
            }).present();
          }
        })
      })
      this.storage.set('arLists', JSON.stringify({ data: data }))
      this.loader.dismiss()
    } else {
      this.dataService.postData(this.baseUrl + this.arCreateUrl, data)
        .subscribe((res) => {
          this.loader.dismiss();
          if (res && res['status']) {
            this.alertCtrl.create({
              subTitle: res['status'],
              buttons: [
                {
                  text: 'OK',
                  role: 'submit',
                  handler: () => {
                    this.navCtrl.setRoot(LandingPage)
                  }
                },
              ],
            }).present();
          }
        })
    }
  }

}



